@import url('https://fonts.googleapis.com/css?family=Raleway');

.todoList {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    cursor: default;

}

header{
    width: 100%;
    color:rgba(223, 249, 251,1.0);
    margin:0;
    text-align: center;
    background-color: rgba(192, 57, 43,1.0);
}

header h1{
    padding: 40px;
    font-size: 70px;
    font-weight: 100;
    font-family: 'Raleway', sans-serif;
}

header h2{
    font-size: 32px;
    font-weight: 100;
    font-family: 'Raleway', sans-serif;
}


.container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align:center;
        align-items:center;
    width: 80%;
    
}

footer{
    margin-top: 60px;
    width: 100%;
    color:rgba(223, 249, 251,1.0);
    background-color: rgba(192, 57, 43,1.0);
    text-align: center;
}

footer h4{
    font-weight: 100;
    font-family: 'Raleway', sans-serif;
    letter-spacing: 3px;
}



