@import url('https://fonts.googleapis.com/css?family=Raleway');


.todoItem{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction:column;
        flex-direction:column;
    -ms-flex-align: center;
        align-items: center;
}

li {
    padding-left: 10px;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    font-family: 'Raleway', sans-serif;
    font-size: 22px;
    font-weight: 600;
    line-height: 150%;
}
.text:hover{
    cursor: pointer;
}


ul {
    padding-left: 10px;
    padding-right: 30px;
    width:100%;
    margin-top: 80px;
}

.text{
    -ms-flex-preferred-size:95%;
        flex-basis:95%;
}
.btn{
    margin-left: 10px;
    opacity: 0.6;
    cursor: pointer;
}
.btn:hover{
    opacity: 1;
}

form.editForm{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: center;
        justify-content: center;
    opacity: 0;
    margin-top: 20px;
    width: 0%;
    height: 0%;
    -webkit-animation: 0.3s fadeIn 0s forwards;
            animation: 0.3s fadeIn 0s forwards;
}
form.editForm input{

    padding: 3px;
    font-size: 18px;
}

@-webkit-keyframes fadeIn{
    100%{
        opacity: 1;
        width: 60%;

    }
}

@keyframes fadeIn{
    100%{
        opacity: 1;
        width: 60%;

    }
}



